import * as React from "react";
import {
    Button,
    Col,
    Icon,
    Layout,
    List,
    Pagination,
    Row,
    Select,
    TreeSelect,
    Steps,
    Modal,
    Divider,
    Timeline
} from "antd";
import Input from "antd/es/input/Input";
import {flatListById} from "../../library/tree-utils";
import {Typography} from 'antd';
import BasePages from "../base/BasePages";
import {request} from "../../library/request";
import global from "../../application/global";
import {Tabs} from 'antd';
import {ConfigAppField} from "../../components/config-appId/ConfigAppField";

const {TabPane} = Tabs;
const {Text} = Typography;
const {Step} = Steps;
const {Content} = Layout;
const InputGroup = Input.Group;
const {Option} = Select;
const {confirm} = Modal;

const pageSize = 8;

const task_status = {
    waited: "",
    running: '',
    aborted: "",
    pending_stop: "",
    successful: '',
    user_stopped: ""
};

const task_status_color = {
    waited: "#FF8C00",
    running: '#FF8C00',
    aborted: "#fb3333",
    pending_stop: "#777",
    successful: '#22cc22',
    user_stopped: "#999"
};

export default class TestExec extends BasePages {

    logger = undefined;
    state = {
        value: undefined,
        plans: [],
        pageTasks: [],
        platform: 'IOS',
        ip: '',
        port: '',
        device_id: "",
        appId:undefined,
        tasks: [],
        task: undefined,
        task_logs: {},
        showAppIdDialog:false,
        appIds:[],
        confirmLoading: false,
    };

    onChange = value => {
        console.log(value);
        this.setState({value});
    };

    get_plans() {
        request.get('/get_plans', null)
            .then((res) => {
                const data = res.data ? res.data : [];
                const temp = {};
                flatListById(data, temp);
                Object.keys(temp).map(item => {
                    const plan = temp[item];
                    plan.title = plan.name;
                    plan.label = plan.name;
                    plan.key = plan.plan_id;
                    plan.value = plan.plan_id;
                    if (plan.children.length === 0)
                        plan.children = undefined
                });
                this.setState({plans: data})
            })
    }

    get_task() {
        request
            .get('/get_all_task', null)
            .then((res) => {
                const data = res.data ? res.data : [];
                let task = undefined;
                data.map(item => {
                    if (!this.state.task) {
                        if (item.status === 'running') {
                            task = item;
                            task.selected = true
                        }
                    } else if (item.task_id === this.state.task.task_id) {
                        task = item;
                        task.selected = true
                    }
                });
                this.setState({tasks: data, task});
                if (task)
                    this.onClickHistory(task, false);
                this.onPagination(1)
            }).finally(() => this.setState({loading: false}));
    }

    componentDidMount() {
        const {ip, port, device_id} = global[this.state.platform];
        this.setState({ip, port, device_id});
        this.setState({loading: true});
        this.get_plans();
        this.get_task();
        this.getAppIds();
        this.scrollToBottom();
    }

    scrollToBottom = () => {
        if (this.messagesEnd)
            this.messagesEnd.scrollIntoView({behavior: "smooth"});
    };

    componentDidUpdate() {
        this.scrollToBottom();
    }


    render() {
        let {task} = this.state;
        const status = task && task.status;
        const report_url = task && task.report_url;
        const page_data = task && task.page_data;
        let has_attach = false;
        let task_step_errors = undefined;
        let step_key = [];
        let step_error_key = [];
        const task_step = task?.task_step ? task.task_step : [];
        const task_log = task && this.state.task_logs ? this.state.task_logs[task.task_id] : [];
        if (task_step) {

            task_step_errors = task_step.filter(item => !item.successful);
            task_step_errors.forEach(item => {
                if (item.attach) has_attach = true
            });
            step_key = task_step.filter(item => item.attach).map(item => item.attach);
            step_error_key = task_step_errors.filter(item => item.attach).map(item => item.attach);

            task_step.forEach(item => {
                const page = page_data.find(page => page.path === item.attach);
                item.page_name = page ? `${page.name}` : '';
                const is_error_page = step_error_key.find(path => path.startsWith(item.attach))
                if (is_error_page)
                    item.successful = false
            });


        }

        let {ip, port, device_id,appId} = this.state;
        if (task && task.app_info) {
            ip = task.app_info.ip;
            port = task.app_info.port;
            device_id = task.app_info.device_id;
            appId = task.app_info.appId;
        }

        return (
            <Layout style={{padding: '2vh', height: "90vh", display: "flex", flexDirection: "column"}}>
                <Content style={{background: "#fff", flex: 1}}>
                    <Row type={'flex'}>
                        <Col span={6}
                             style={{
                                 height: "88vh",
                                 display: "flex",
                                 flexDirection: "column",
                                 borderRight: "0.1px solid",
                                 borderColor: "#efefef",
                             }}>

                            <div style={{width: "100%", height: '6vh'}}>

                                <div
                                    style={{
                                        width: "100%",
                                        height: "100%",
                                        display: "flex",
                                        paddingTop: '2vh',
                                        flexDirection: "column",
                                        alignItems: 'center',
                                        justifyContent: "center",
                                        zIndex: 0
                                    }}>
                                    <p style={{fontSize: 15, color: "#777"}}></p>
                                </div>
                            </div>

                            <div style={{background: "#efefef", height: 1, width: "100%"}}/>
                            <div className={'hide-scrollbar'} style={{height: '75vh', overflowY: 'scroll'}}>
                                <List
                                    size="small"
                                    itemLayout="vertical"
                                    dataSource={this.state.pageTasks}
                                    renderItem={item => {
                                        const status_color = task_status_color[item.status];
                                        return <Row type="flex" justify="start"
                                                    onClick={() => this.onClickHistory(item)}
                                                    style={{
                                                        background: item.selected ? `#ddd` : "#fff",
                                                        marginRight: 1
                                                    }}
                                        >
                                            <Row type="flex" justify="start" align={'middle'}
                                                 style={{
                                                     paddingLeft: 16,
                                                     paddingRight: 16,
                                                     paddingTop: 12,
                                                     paddingBottom: 12,
                                                     flex: 1
                                                 }}>
                                                <Icon type={item.device.platform === "Android" ? 'android' : "apple"}
                                                      style={{
                                                          fontSize: 20,
                                                          color: item.status === 'user_stopped' ? task_status_color.user_stopped : "#333"
                                                      }}/>
                                                <Row style={{flex: 1, marginLeft: 8}}>
                                                    <Col>
                                                    <span className={'item-title'} style={{
                                                        color: item.status === 'user_stopped' ? task_status_color.user_stopped : "#333",
                                                        fontSize: 14
                                                    }}>{item.name}</span>
                                                    </Col>
                                                    <Col>
                                                        <span style={{
                                                            color: item.status === 'user_stopped' ? task_status_color.user_stopped : "#777",
                                                            fontSize: 10
                                                        }}>{item.time}</span>
                                                    </Col>
                                                </Row>
                                                <span style={{
                                                    color: status_color,
                                                    fontSize: 14
                                                }}>{task_status[item.status]}</span>
                                            </Row>
                                            <div style={{background: "#efefef", height: 1, width: "100%"}}/>
                                        </Row>
                                    }}
                                />
                            </div>
                            <Row type={'flex'} justify={'center'} style={{height: '4vh'}}>
                                <Pagination
                                    simple={true}
                                    size="small"
                                    total={this.state.tasks ? this.state.tasks.length : 0}
                                    pageSize={pageSize}
                                    defaultCurrent={1}
                                    showQuickJumper
                                    onChange={(pageNum) => this.onPagination(pageNum)}

                                />
                            </Row>
                        </Col>
                        <Col span={18} style={{height: '85vh'}}>
                            {
                                (task === undefined || status === 'running' || status === 'waited') ?
                                    <div style={{
                                        display: "flex",
                                        flexDirection: "column",
                                        height: "100%",
                                    }}>
                                        <Row type="flex" justify="start"
                                             style={{marginTop: 32, marginLeft: 32}}
                                        >
                                            <InputGroup compact>
                                                <Select defaultValue="IOS" style={{width: 100}}
                                                        onChange={(platform) => {
                                                            const {ip, port, device_id,appId} = global[platform];
                                                            this.setState({platform, ip, port, device_id,appId});
                                                        }}>
                                                    <Option value="Android">Android</Option>
                                                    <Option value="IOS">IOS</Option>
                                                </Select>

                                                <Input placeHolder="127.0.0.1"
                                                       value={ip}
                                                       onChange={(e) => {
                                                           global[this.state.platform].ip = e.target.value;
                                                           this.setState({ip: e.target.value})
                                                       }}
                                                       style={{width: 120}}/>
                                                <Input placeHolder="8100"
                                                       value={port}
                                                       onChange={(e) => {
                                                           global[this.state.platform].port = e.target.value;
                                                           this.setState({port: e.target.value})
                                                       }}
                                                       style={{width: 80}}/>
                                                <Input addonBefore=""
                                                       value={device_id}
                                                       onChange={(e) => {
                                                           global[this.state.platform].device_id = e.target.value;
                                                           this.setState({device_id: e.target.value})
                                                       }}
                                                       style={{marginLeft: 8, width: 200}}/>

                                                <Select style={{marginLeft: 8, width: 200}}
                                                        placeholder=""
                                                        value={appId}
                                                        onChange={(appId) => {
                                                            global[this.state.platform].appId=appId;
                                                            this.setState({appId})
                                                        }}
                                                        dropdownRender={menu => (
                                                            <div>
                                                                {menu}
                                                                <Divider style={{margin: '4px 0'}}/>
                                                                <div
                                                                    style={{padding: '4px 8px', cursor: 'pointer'}}
                                                                    onMouseDown={e => e.preventDefault()}
                                                                    onClick={this.showAppIdDialog}
                                                                >
                                                                    <Icon type="setting"/> 
                                                                </div>
                                                            </div>
                                                        )}>
                                                    {
                                                        this.state.appIds.map(item=>(
                                                            <Option value={item.name}>{item.name}</Option>
                                                        ))
                                                    }
                                                </Select>

                                            </InputGroup>


                                        </Row>
                                        <Row type="flex" justify="start"
                                             style={{marginTop: 16, marginLeft: 16}}
                                        >
                                            <TreeSelect
                                                style={{minWidth: 150, marginLeft: 16}}
                                                dropdownStyle={{maxHeight: 400, overflow: 'auto'}}
                                                treeData={this.state.plans}
                                                placeholder=""
                                                treeDefaultExpandAll
                                                onSelect={this.onChange}
                                            />
                                            {
                                                this.state.task && this.state.task.status === 'running' ? <div
                                                    onClick={() => this.stopTest()}
                                                    style={{
                                                        width: 20,
                                                        height: 20,
                                                        marginLeft: 16,
                                                        borderRadius: 2,
                                                        background: "#cc2222",
                                                        marginTop: 4
                                                    }}/> : <Icon type={'caret-right'}
                                                                 onClick={() => this.startTest()}
                                                                 style={{
                                                                     color: '#229922',
                                                                     fontSize: 32,
                                                                     marginLeft: 8
                                                                 }}/>
                                            }

                                        </Row>
                                        <div

                                            className={'hide-scrollbar'}
                                            style={{
                                                marginTop: 24, marginLeft: 32,
                                                flex: 1,
                                                background: "#111",
                                                padding: 8,
                                                overflowY: 'scroll'
                                            }}>
                                            {
                                                task_log && task_log.map(item => {
                                                    return <div
                                                        ref={(el) => {
                                                            this.messagesEnd = el;
                                                        }}
                                                        style={{marginTop: 4, color: "#ffff"}}>
                                                        {
                                                            <Text code={true}
                                                                  style={{
                                                                      color: item.type === 'error' ? void 0 : "#fff",
                                                                      fontSize: 14
                                                                  }}
                                                                  type={item.type === 'error' ? "danger" : undefined}>{`${item.time}${item.log}`}</Text>
                                                        }

                                                    </div>
                                                })
                                            }
                                        </div>
                                    </div> :
                                    <div style={{
                                        display: "flex",
                                        flexDirection: "column",
                                        height: "100%",
                                    }}>
                                        <Row type={'flex'} justify={'start'} style={{
                                            paddingTop: 24,
                                            paddingLeft: 24,
                                            paddingRight: 24,
                                            paddingBottom: 16
                                        }}>
                                            <Button type="primary" ghost icon={'left'}
                                                    onClick={() => this.getBackControl()}>
                                                
                                            </Button>


                                            <Row type={'flex'} justify={'end'} style={{flex: 1}}>
                                                <Button style={{marginLeft: 16}} type={'danger'}
                                                        onClick={() => confirm({
                                                            title: `${task.name}?`,
                                                            content: '',
                                                            onOk: () => this.onDel(),
                                                        })}></Button>

                                                <Button type="primary" style={{marginLeft: 16}}
                                                        onClick={() => this.onRedo()}>
                                                    
                                                    <Icon type="redo"/>
                                                </Button>

                                                {
                                                    has_attach && <Button type="primary" style={{marginLeft: 16}}
                                                                          onClick={() => this.onRedoError()}>
                                                        
                                                        <Icon type="exclamation-circle"/>
                                                    </Button>
                                                }

                                                {
                                                    report_url && <Button type="primary" style={{marginLeft: 16}}
                                                                          onClick={() => this.goToReport(report_url)}>
                                                        
                                                        <Icon type="right"/>
                                                    </Button>
                                                }
                                            </Row>
                                        </Row>
                                        <Tabs defaultActiveKey="2" style={{height: '75vh'}}>
                                            <TabPane tab="" key="1">
                                                <div className={'hide-scrollbar'}
                                                     style={{overflowY: 'scroll', padding: 24, height: '70vh'}}>
                                                    <Timeline>
                                                        {
                                                            page_data && page_data.map(item => {
                                                                let pageColor = step_key.includes(item.path) ? 'blue' : 'gray';
                                                                let pageTitleColor = '#222';

                                                                const method_item = item.method_data.map(method => {
                                                                    let methodColor = '#222';
                                                                    if (step_error_key.includes(method.path)) {
                                                                        pageColor = 'red';
                                                                        pageTitleColor = '#f5222d';
                                                                        methodColor = '#f5222d'
                                                                    }
                                                                    return <span><br/>
                                                                        <span
                                                                            style={{color: methodColor}}>  {method.name} </span>
                                                                        <span
                                                                            style={{color: "#777"}}>{`(${method.data.map(data => (`${data.name}:${data.value}`))})`}</span>
                                                                        </span>
                                                                });


                                                                return <Timeline.Item color={pageColor}>
                                                                    <span
                                                                        style={{color: pageTitleColor}}>{item.alias || item.name}</span>
                                                                    <span
                                                                        style={{color: "#777"}}>{`(${item.data.map(data => (`${data.name}:${data.value}`))})`}</span>
                                                                    {method_item}
                                                                </Timeline.Item>
                                                            })
                                                        }
                                                    </Timeline>
                                                </div>
                                            </TabPane>
                                            <TabPane tab="" key="2">
                                                <div className={'hide-scrollbar'}
                                                     style={{overflowY: 'scroll', padding: 24, height: '70vh'}}>
                                                    <Steps direction="vertical" size="small">
                                                        {
                                                            task_step?.map(item => (
                                                                <Step title={`${item.title}${item.page_name}`}
                                                                      subTitle={`${item.sub_title}`}
                                                                      description={item.message}
                                                                      status={!item.successful ? 'error' : 'finish'}/>))
                                                        }
                                                    </Steps>
                                                </div>
                                            </TabPane>
                                            {
                                                task_step_errors && task_step_errors.length > 0 &&
                                                <TabPane tab="" key="3">
                                                    <div className={'hide-scrollbar'}
                                                         style={{overflowY: 'scroll', padding: 24, height: '70vh'}}>
                                                        <Steps direction="vertical" size="small">
                                                            {
                                                                task_step_errors?.filter(item => !item.successful).map(item => (
                                                                    <Step title={`${item.title}${item.page_name}`}
                                                                          subTitle={`${item.sub_title}`}
                                                                          description={item.message}
                                                                          status={!item.successful ? 'error' : 'finish'}/>))
                                                            }
                                                        </Steps>
                                                    </div>
                                                </TabPane>
                                            }
                                        </Tabs>
                                    </div>
                            }
                        </Col>

                    </Row>

                    <Modal
                        title=""
                        visible={this.state.showAppIdDialog}
                        confirmLoading={this.state.confirmLoading}
                        onOk={this.onSaveAppIds}
                        onCancel={()=> this.setState({
                            showAppIdDialog: false,
                        })}
                    >
                        <ConfigAppField
                            formRef={ref=>this.appIdFormRef = ref}
                            dataSource={this.state.appIds}
                        />
                    </Modal>
                </Content>
            </Layout>
        );
    }


    onPagination(pageNum) {
        let pageTasks = [];
        if (this.state.tasks)
            pageTasks = this.state.tasks.slice((pageNum - 1) * pageSize, (pageNum - 1) * pageSize + pageSize);
        this.setState({pageTasks});

    }

    onClickHistory(task, isManual = true) {
        console.log(task);
        const tasks = [...this.state.tasks];
        tasks.map(item => item.selected = false);
        task.selected = true;
        const log = isManual ? {test_log: [], test_step: []} : {};
        this.setState({tasks, task, ...log});
        if (this.logger) clearInterval(this.logger);

        if (task.status === 'running')
            this.logger = setInterval(() => this.getTaskLog(task), 1000);
        else if (isManual && task.status === 'waited')
            setTimeout(() => this.get_task(), 500);
        else
            this.getTaskStep(task)
    }

    componentWillUnmount() {
        if (this.logger) clearInterval(this.logger);
    }

    startTest() {
        const {ip, port, device_id} = global[this.state.platform];
        request
            .post('/start_test', {
                ip,
                port,
                device_id,
                app_id:this.state.appId,
                platform: this.state.platform,
                plan_id: this.state.value,
            })
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    this.get_task();
                    setTimeout(() => this.get_task(), 2500);
                }
            });
    }

    stopTest() {
        const {task} = this.state;
        clearInterval(this.logger);
        if (!task || task.status !== 'running') {
            return
        }
        request
            .post('/stop_test', {task_id: task.task_id, group_id: task.group_id})
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    this.get_task();
                    setTimeout(() => this.get_task(), 2500);
                }
            });
    }

    getTaskLog(task) {
        if (!task || task.status !== 'running') {
            clearInterval(this.logger);
            return
        }
        request
            .post('/get_task_log', {task_id: task.task_id, group_id: task.group_id}, {errorTip: false})
            .then((res) => {
                const task_log = [];
                const data = res;
                if (data) {
                    data.forEach(log => {
                        task_log.push(JSON.parse(log))
                    });
                }

                const task_logs = this.state.task_logs;
                task_logs[task.task_id] = task_log;
                this.setState({task_logs});

                const currentTask = this.state.task;
                if (currentTask && currentTask.task_id === task.task_id) {
                    this.scrollToBottom();
                    this.get_task()
                }
            })
    }

    getTaskStep(task) {
        if (!task) return;
        if (task.task_steps) return;
        request
            .post('/get_task_step', {task_id: task.task_id, group_id: task.group_id})
            .then((res) => {
                const task_step = [];
                const data = res;
                if (data) {
                    data.forEach(step => {
                        task_step.push(JSON.parse(step))
                    });
                }
                const currentTask = this.state.task;
                if (currentTask && currentTask.task_id === task.task_id) {
                    currentTask.task_step = task_step;
                    this.setState({task_steps: currentTask});
                }
            })
    }

    getAppIds() {
        request
            .get('/get_appIds')
            .then((res) => {
                const data = res.data;
                if (data && Array.isArray(data) && data.length > 0) {
                    this.setState({appIds: data});
                }
            })
    }


    getBackControl() {
        const {task} = this.state;
        const tasks = [...this.state.tasks];
        tasks.map(item => item.selected = false);
        task.selected = false;
        this.setState({tasks, task: void 0, task_log: []});
    }

    onDel() {
        const {task} = this.state;
        if (!task) return;
        request
            .post('/del_task', {task_id: task.task_id, group_id: task.group_id})
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    this.get_task();
                    setTimeout(() => this.get_task(), 2500);
                }
            });
    }

    onRedo() {
        const {task} = this.state;
        if (!task) return;
        request
            .post('/start_test_by_task', {task_id: task.task_id, group_id: task.group_id})
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    this.get_task();
                    setTimeout(() => this.get_task(), 2500);
                }
            });
    }

    onRedoError() {
        const {task} = this.state;
        if (!task) return;
        const task_step = task.task_step;
        const selected_node = [];
        if (task_step)
            task_step.filter(item => !item.successful).forEach(item => {
                if (item.attach) selected_node.push(item.attach)
            });
        request
            .post('/start_error_test_by_task', {
                task_id: task.task_id,
                group_id: task.group_id,
                selected_node: selected_node
            })
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    this.get_task();
                    setTimeout(() => this.get_task(), 2500);
                }
            });


    }

    goToReport(report_url) {
        const w = window.open('about:blank');
        w.location.href = report_url
    }

    showAppIdDialog = () => {
        this.setState({ showAppIdDialog: true})
    }


    onSaveAppIds=()=> {
        this.setState({ confirmLoading: true});
        if(this.appIdFormRef){
            this.appIdFormRef.validateFields((err, values) => {
                if (!err) {
                    console.log('values',values);
                    const appIds=Object.keys(values.name).map(item=>({id:item,name:values.name[item]}));
                    request.post('upload_appIds',{appIds}).then((res) => {
                        const code = res.code;
                        if (code === 1) {
                            this.setState({appIds});
                            this.setState({ showAppIdDialog: false, confirmLoading: false})
                        }else
                            this.setState({ confirmLoading: false})
                    }).catch(()=>this.setState({ confirmLoading: false}));

                }else{
                    this.setState({ confirmLoading: false})
                }
            });
        }

    }

}


