import React, {Component} from 'react';
import {
    Form,
    Row,
    Col,
    Button,
    Popconfirm,
} from 'antd';
import Operator from "../operator";
import TableEditable from "../table-editable";

export default class ConfigPageField extends Component {
    state = {
        dataSource: [],
        pageName: "",
        pageInfo: undefined,
    };

    fieldsColumns = [
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            width: '33%',
            props: {
                type: 'input',
                placeholder: '',
                decorator: {
                    rules: [
                        {required: true, message: ''},
                        {
                            validator: (rule, value, callback) => {

                                const reg = RegExp(/^(_|[a-z]|[A-Z]).*/);
                                if (value && !reg.test(value)) {
                                    callback('');
                                    return
                                }
                                callback()

                            },
                        }
                    ],
                },
                onPressEnter: (e) => {
                    const currentTr = e.target.parentNode.parentNode.parentNode.parentNode.parentNode.parentNode.parentNode;
                    currentTr.getElementsByTagName('input')[1].focus();
                },
            },
        },
        {
            title: '',
            dataIndex: 'value',
            key: 'value',
            width: '33%',
            props: {
                type: 'input',
                placeholder: '',
                decorator: {
                    rules: [
                        {required: true, message: ''},
                    ],
                    onKeyUp: (e) => {
                        console.log(e);
                    },
                },
            },
        },
        {
            title: '',
            render: (text, record) => {
                //  showEdit  editable true
                const {showEdit = true, editable = true} = record;
                const _that = this;

                const {disable} = this.props;
                if (disable) return void 0;

                if (showEdit && editable) {
                    return (
                        <Operator items={[
                            {
                                label: '', onClick: () => {
                                    record.save((dataSource, process) => {
                                        console.log(dataSource);
                                        const newDataSource = dataSource.filter(item => item.name).map(item => ({
                                            id: item.id,
                                            name: item.name,
                                            value: item.value
                                        }));
                                        const onSave = _that.props.onSave;
                                        if (onSave) onSave(newDataSource, process);
                                        else process()

                                    });
                                }
                            },
                            {label: '', onClick: record.cancel},
                        ]}/>
                    );
                }
                const {id, value, name} = record;

                function del() {
                    const delItems = [_that.state.dataSource.find(item => item.id === id)].map(item => ({
                        id: item.id,
                        name: item.name,
                        value: item.value
                    }));
                    const onDel = _that.props.onDel;
                    if (onDel) onDel(delItems, () => {
                        const dataSource = _that.state.dataSource.filter(item => item.id !== id);
                        _that.setState({dataSource});
                    });
                    else {
                        const dataSource = _that.state.dataSource.filter(item => item.id !== id);
                        _that.setState({dataSource});
                    }

                }

                const deleteItem = {
                    label: '',
                    color: 'red',
                    confirm: {
                        title: `"${name || value}"`,
                        onConfirm: del,
                    },
                };

                // 
                if (!value && !name) {
                    Reflect.deleteProperty(deleteItem, 'confirm');
                    deleteItem.onClick = del;
                }
                return (
                    <Operator items={[
                        {
                            label: '',
                            disabled: editable === false,
                            onClick: () => {
                                const dataSource = [..._that.state.dataSource];
                                const r = dataSource.find(item => item.id === record.id);
                                r.showEdit = true;
                                _that.setState({dataSource});
                            }
                        },
                        deleteItem
                    ]}/>
                );
            }
        }
    ];

    componentWillReceiveProps(nextProps) {
        if (!this.comparePageInfo(this.props.pageInfo, nextProps.pageInfo)) {
            const pageInfo = nextProps.pageInfo;
            if (pageInfo) {
                pageInfo.data.forEach(item=>(item.showEdit= false));
                console.log('componentWillReceiveProps',pageInfo.data);
                this.setState({
                    dataSource: pageInfo.data,
                    pageName: `${pageInfo.title}`,
                    pageInfo: pageInfo
                });
            }
        }
    }

    comparePageInfo(a, b) {
        if (a.key !== b.key)
            return false;
        if (a.data.length !== b.data.length)
            return false;
        for (const i in a.data) {
            if (a.data[i].name !== b.data[i].name || a.data[i].value !== b.data[i].value)
                return false;
        }
        return true;

    }


    componentWillMount() {
        const {formRef, form, validate, pageInfo} = this.props;
        if (formRef) formRef(form);
        if (validate) validate(this.validate);

        if (pageInfo) {
            for (const index in pageInfo.data) {
                pageInfo.data[index].showEdit = false
            }

            this.setState({
                dataSource: pageInfo.data,
                pageName: `${pageInfo.title}`,
                pageInfo: pageInfo,
            })
        }
    }

    validate = () => {
        const {form} = this.props;

        const promises = [
            form, this.fieldsTableForm,
        ].map(item => new Promise((resolve, reject) => {
            item.validateFieldsAndScroll((err, values) => {
                if (err) {
                    reject(err);
                } else {
                    resolve(values)
                }
            });
        }));

        return Promise.all(promises).then(([listPage]) => listPage);
    };

    renderTableTitle = (field) => {
        const {disable} = this.props;
        return (
            <div>
                <p style={{fontSize: 20, color: "#333", fontWeight: "bold"}}>{`${this.state.pageName}`}</p>
                {(!disable) && <Row type={'flex'} justify={'end'} style={{marginBottom: 8,marginRight:16}}>
                    <Col>
                        <this.ClearTable field={field} type={'link'}/>
                    </Col>
                    <Col>
                        <a style={{marginLeft: 8, color: '#1890ff'}} type="primary" onClick={() => this.handleAdd()}></a>
                    </Col>
                </Row>}
            </div>
        );
    };

    ClearTable = ({field, type = 'danger'}) => {
        const {dataSource} = this.state;
        const isEmpty = !dataSource || !dataSource.length || (dataSource.length === 1 && !dataSource[0].title);

        if (isEmpty) return null;

        return (
            <Popconfirm title="" onConfirm={() => this.setState({dataSource: []})}>
                {type === 'link' ? (
                    <a style={{marginLeft: 8, color: 'red'}}></a>
                ) : (
                    <Button style={{marginLeft: 8}} type={type}></Button>
                )}
            </Popconfirm>
        );
    };


    handleAdd = () => {
        this.tab.handleAddNewRow()
    };
    handleChange = (dataSource) => {
        this.setState({dataSource});
    };


    render() {
        const {emptyText=''}=this.props;
        return (
            <div style={{padding: 16, background: "#fff", height: "100%"}}>
                {
                    this.renderTableTitle('fields')
                }
                <Form>
                    <TableEditable
                        tabRef={ref => this.tab = ref}
                        style={{background: "#fff"}}
                        size="small"
                        emptyText={emptyText}
                        onChange={this.handleChange}
                        dataSource={this.state.dataSource}
                        showAdd={false}
                        formRef={form => this.fieldsTableForm = form}
                        columns={this.fieldsColumns}
                    />
                </Form>
            </div>
        );
    }
}
