import { Menu, Dropdown} from 'antd';
import * as React from "react";

export default class extends React.Component {

    menu = (
        <Menu onClick={this.props.onClick}>
            <Menu.Item key="add"></Menu.Item>
            <Menu.Item key="copy"></Menu.Item>
            <Menu.Item key="del"></Menu.Item>
        </Menu>
    );

    render() {
        const {menu, children, onClick} = this.props;
        const menuView = (
            <Menu>
                {menu.map(item =>
                    <Menu.Item onClick={onClick} key={item.key}>{item.name}</Menu.Item>
                )}
            </Menu>
        );
        // {/*trigger={['contextMenu']}*/}
        return (
            <Dropdown {...this.props} overlay={menuView}>
                {children}
            </Dropdown>
        );
    }
}
