import { Form, Input, Icon, Button } from 'antd';
import React from "react";
import uuid from "uuid/v4";


class DynamicFieldSet extends React.Component {


    componentDidMount(){
        const {form,formRef} = this.props;
        formRef && formRef(form);
        const {dataSource} = this.props;
        const newSource = dataSource.map(item=>item.id);
        form.setFieldsValue({dataSource:newSource});
        const name = {};
        dataSource.forEach(item =>{ name[item.id] = item.name});
        this.setState({name:name});
    }

    remove = k => {
        const { form } = this.props;
        const dataSource = form.getFieldValue('dataSource');
        if (dataSource.length === 1) {
            return;
        }
        form.setFieldsValue({
            dataSource: dataSource.filter(key => key !== k),
        });
    };

    add = () => {
        const { form } = this.props;
        const dataSource = form.getFieldValue('dataSource');
        const nextDataSource = dataSource.concat(uuid());
        form.setFieldsValue({
            dataSource: nextDataSource,
        });
    };



    render() {
        const { getFieldDecorator, getFieldValue } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 4 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 20 },
            },
        };
        const formItemLayoutWithOutLabel = {
            wrapperCol: {
                xs: { span: 24, offset: 0 },
                sm: { span: 20, offset: 4 },
            },
        };
        getFieldDecorator('dataSource', { initialValue: [] });
        const dataSource = getFieldValue('dataSource');
        const formItems = dataSource.map((item, index) => {
            return <Form.Item
                {...(index === 0 ? formItemLayout : formItemLayoutWithOutLabel)}
                label={index === 0 ? '' : ''}
                required={false}
                key={item}
            >
                {getFieldDecorator(`name[${item}]`, {
                    validateTrigger: ['onChange', 'onBlur'],
                    initialValue:this.state.name[item],
                    rules: [
                        {
                            required: true,
                            whitespace: true,
                            message: "",
                        },
                    ],
                })(<Input placeholder="" style={{ width: '60%', marginRight: 8 }} />)}
                {dataSource.length > 1 ? (
                    <Icon
                        className="dynamic-delete-button"
                        type="minus-circle-o"
                        onClick={() => this.remove(item)}
                    />
                ) : null}
            </Form.Item>
        });
        return (
            <Form >
                {formItems}
                <Form.Item {...formItemLayoutWithOutLabel}>
                    <Button type="dashed" onClick={this.add} style={{ width: '60%' }}>
                        <Icon type="plus" /> 
                    </Button>
                </Form.Item>
            </Form>
        );
    }
}

export const ConfigAppField = Form.create({ name: 'dynamic_form_item' })(DynamicFieldSet);
