import axios from 'axios';
import {stringify} from 'qs';
import handleError from './handle-error';
import handleSuccess from './handle-success';


export default class Request {
    /**
     * 
     * axiossxAjax
     * sxAjax.defaults.xxx sxAjax.mockDefaults.xxx
     *
     * @param onShowErrorTip 
     * @param onShowSuccessTip 
     * @param isMock mockurl'/mock'mock
     */
    constructor({
                    onShowSuccessTip = (/* response, successTip  */) => true,
                    onShowErrorTip = (/* err, errorTip */) => true,
                    isMock = (/* url, data, method, options */) => false,
                } = {}) {
        this.instance = axios.create();
        this.mockInstance = axios.create();
        this.setDefaultOption(this.instance);
        this.setDefaultOption(this.mockInstance);
        this.defaults = this.instance.defaults;

        this.onShowSuccessTip = onShowSuccessTip;
        this.onShowErrorTip = onShowErrorTip;
    }

    setDefaultOption(instance) {
        instance.defaults.timeout = 10000;
        // instance.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
        // instance.defaults.headers.put['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
        instance.defaults.headers.post['Content-Type'] = 'application/json';
        instance.defaults.headers.put['Content-Type'] = 'application/json';
        instance.defaults.baseURL = '/';
        // instance.defaults.withCredentials = true; // cookie
    }

    /**
     *
     * @param url
     * @param d
     * @param method
     * @param options successTipurl data method
     * @returns {Promise}
     */
    request(url, d = {}, method = 'get', options = {}) {
        //  null
        let data = d || {};
        options = options || {};

        let {
            successTip = false, // false
            errorTip, //  = method === 'get' ? '' : '', // 
            noEmpty = false,
        } = options;

        //   null '' undefined 
        if (noEmpty === true && typeof data === 'object' && !Array.isArray(data)) {
            const noEmptyData = {};

            Object.keys(data).forEach(key => {
                const value = data[key];
                if (value !== null && value !== '' && value !== void 0) {
                    noEmptyData[key] = value;
                }
            });

            data = noEmptyData;
        }

        const CancelToken = axios.CancelToken;
        let cancel;

        const isGet = method === 'get';
        let instance = this.instance;

        /*
         * 
         * axiosInstance.interceptors.request.use(cfg => {
         *   // Do something before request is sent
         *   return cfg;
         * }, error => {
         *   // Do something with request error
         *   return Promise.reject(error);
         * });
         *
         * */

        /*
        *
        * Content-Type application/x-www-form-urlencoded 
        * https://github.com/axios/axios/issues/362
        *
        * */
        const defaultsContentType = instance.defaults.headers[method]['Content-Type'] || '';
        const contentType = (options.headers && options.headers['Content-Type']) || '';
        if (
            (defaultsContentType && defaultsContentType.indexOf('application/x-www-form-urlencoded') > -1)
            || contentType.indexOf('application/x-www-form-urlencoded') > -1
        ) {
            data = stringify(data);
        }

        let params = {};
        if (isGet) {
            params = data; // params geturl
            data = {}; // data putpost 
        }

        const ajaxPromise = new Promise((resolve, reject) => {
            instance({
                method,
                url,
                data,
                params,
                cancelToken: new CancelToken(c => cancel = c),
                ...options,
            }).then(response => {

                if (response.data.code && response.data.code !== 1) {
                    const desc = response.data.desc;
                    this.onShowErrorTip(response.data, desc ? desc : "");
                    reject(response.data);
                }
                else {
                    let localTips = successTip;
                    if (!localTips && response.data.desc)
                        localTips = response.data.desc;
                    this.onShowSuccessTip(response, localTips);
                    resolve(response.data, response);
                }
            }, err => {
                const isCanceled = err && err.message && err.message.canceled;
                if (isCanceled) return; // cancel
                this.onShowErrorTip(err, errorTip);
                reject(err);
            }).catch(error => {
                reject(error);
            });
        });
        ajaxPromise.cancel = function () {
            cancel({
                canceled: true,
            });
        };
        return ajaxPromise;
    }

    /**
     * get
     * @param {string} url 
     * @param {object} [params] query string url
     * @param {object} [options] axios 
     * @returns {Promise}
     */
    get(url, params, options) {
        return this.request(url, params, 'get', options);
    }

    /**
     * post
     * @param {string} url 
     * @param {object} [data] 
     * @param {object} [options] axios 
     * @returns {Promise}
     */
    post(url, data, options) {
        return this.request(url, data, 'post', options);
    }


    /**
     * put
     * @param {string} url 
     * @param {object} [data] 
     * @param {object} [options] axios 
     * @returns {Promise}
     */
    put(url, data, options) {
        return this.request(url, data, 'put', options);
    }

    /**
     * patch
     * @param {string} url 
     * @param {object} [data] 
     * @param {object} [options] axios 
     * @returns {Promise}
     */
    patch(url, data, options) {
        return this.request(url, data, 'patch', options);
    }

    /**
     * deleteparamshttp
     * @param {string} url 
     * @param {object} [data] 
     * @param {object} [options] axios 
     * @returns {Promise}
     */
    del(url, data, options) {
        return this.request(url, data, 'delete', options);
    }
}


/**
 * ajaxerrorTip  successTip
 * @type {Request}
 */
export const request = new Request({
    onShowErrorTip: (error, errorTip) => handleError({error, errorTip}),
    onShowSuccessTip: (response, successTip) => handleSuccess({successTip}),
});

// 
request.defaults.baseURL = 'http://localhost:9000';
request.defaults.timeout = 1000 * 30;


// 
request.instance.interceptors.request.use(cfg => {
    // Do something before request is sent
    return cfg;
}, error => {
    // Do something with request error
    return Promise.reject(error);
});


