import {Modal, Form, Input, Select} from 'antd';
import * as React from "react";

const {Option} = Select;

class FromModal extends React.Component {

    state = {
        confirmLoading: false
    };

    handlerOk = () => {
        const {form, onOk} = this.props;
        form.validateFields((err, values) => {
            if (err) {
                return;
            }
            if (onOk) {
                this.setState({confirmLoading: true});
                onOk(values, (isRest) => {
                    if (isRest) form.resetFields();
                    this.setState({confirmLoading: false})
                })
            }
        });
    };

    render() {
        const {visible, onCancel, form, title, okText, fields, maskClosable = false} = this.props;
        const {getFieldDecorator} = form;
        return (
            <Modal
                visible={visible}
                title={title}
                okText={okText}
                maskClosable={maskClosable}
                onCancel={onCancel}
                confirmLoading={this.state.confirmLoading}
                onOk={this.handlerOk}
            >
                <Form layout="vertical">
                    {
                        fields.filter(item => !item.disabled).map((item) => (
                            <Form.Item label={item.label} key={item.field}
                                       style={{width: item.width ? item.width : "100%"}}

                            >
                                {
                                    getFieldDecorator(item.field, {...item.props ? item.props.decorator : {}})(
                                        item.selectData ? <Select
                                            onChange={(value) => {
                                                this.props.form.setFieldsValue({[item.field]: value})
                                            }}
                                        >
                                            {item.selectData.map((item, index) => (
                                                <Option value={item.value} key={index}>{item.name}</Option>))}
                                        </Select> : <Input {...item.props}  />)
                                }
                            </Form.Item>
                        ))
                    }
                </Form>
            </Modal>
        );
    }
}

export default Form.create()(FromModal)
