import {Component} from "react";
import React from "react";
import {Layout, Menu, Icon} from 'antd';
import './style.less';

const {Header, Sider, Content} = Layout;

const menus = [{
    icon: 'user',
    title: "",
    path: "/test_pages"
}, {
    icon: 'user',
    title: "",
    path: "/test_plans"
}, {
    icon: 'user',
    title: "",
    path: "/test_exec"
}, {
    icon: 'user',
    title: "",
    path: "/guide"
}];

export class Application extends Component {
    state = {
        collapsed: false,
    };

    toggle = () => {
        this.setState({
            collapsed: !this.state.collapsed,
        });
    };

    defaultSelectedKeys = [];

    render() {
        let {pathname} = this.props.location;
        if (pathname === '/') pathname = "/test_pages";

        this.defaultSelectedKeys.pop();
        this.defaultSelectedKeys.push(pathname);

        return (
            <Layout>
                <Sider trigger={null} collapsible collapsed={this.state.collapsed}>
                    <div style={{
                        display: 'flex',
                        height: 32,
                        justifyContent: "center",
                        alignItem: 'center',
                        background: ' rgba(255, 255, 255, 0.2)',
                        margin: 16
                    }}>
                        {/*<p style={{color: "#fff", fontSize: 20}}>{pathname}</p>*/}
                        <p style={{color: "#fff", fontSize: 20}}>{this.state.collapsed ? 'YA' : 'YA-TEST'}</p>
                    </div>
                    <Menu theme="dark" mode="inline" defaultSelectedKeys={this.defaultSelectedKeys}>
                        {
                            menus && menus.map((item) => {
                                return <Menu.Item key={item.path} onClick={() => this.props.history.replace(item.path)}>
                                    <Icon type={item.icon}/>
                                    <span>{item.title}</span>
                                </Menu.Item>
                            })
                        }
                    </Menu>
                </Sider>
                <Layout id='rootLayout'>
                    <Header style={{background: '#fff', padding: 0}}>
                        <Icon
                            style={{
                                fontSize: '18px',
                                lineHeight: '64px',
                                padding: '0 24px',
                                cursor: 'pointer',
                                transition: ' color 0.3s',
                            }}
                            type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
                            onClick={this.toggle}
                        />
                    </Header>
                    <Content>
                        {this.props.children}
                    </Content>
                </Layout>
            </Layout>
        );
    }

}
