import * as React from "react";
import * as antd from "antd";
import {Col, Row, Modal, notification} from "antd";
import {flatListById, flatListByKey, getParentKey} from "../../library/tree-utils";
import {Layout} from "antd";
import {Icon} from "antd";
import {Menu} from "antd";
import RightClickMenu from "../../components/RightClickMenu";
import FromModal from "../../components/FromModal";
import {Empty} from "antd";
import {Checkbox} from "antd";
import ConfigPageField from "../../components/config-page/ConfigPageField";
import BasePages from "../base/BasePages";
import {request} from "../../library/request";

const {Content} = Layout;
const {confirm} = Modal;
const {Tree} = antd;
const {TreeNode} = Tree;


export default class TestPlans extends BasePages {

    flat_menu_data = {};
    flat_pages_data = {};

    state = {
        expandedKeys: [],
        autoExpandParent: true,
        checkedKeys: [],
        selectedKeys: [],
        menuSelectedKeys: [],
        menuItemId: undefined,
        showAddPlanModal: false,
        showAddSubPlanModal: false,
        showCopyPlanModal: false,
        pages: [],
        data: [],
        versions: [],
        allCheckStatus: false
    };

    plan_modal_field = [
        {
            label: "",
            field: "name",
            props: {decorator: {rules: [{required: true, message: ""}]}}
        },
        {
            label: "",
            field: "version_id",
            props: {decorator: {rules: [{required: true, message: ""}]}}
        }
    ];
    sub_plan_modal_field = [
        {
            label: "",
            field: "parent_name",
            props: {disabled: true, decorator: {}}
        },
        {
            label: "",
            field: "version_name",
            props: {disabled: true, decorator: {}}
        },
        {
            label: "",
            field: "name",
            props: {decorator: {rules: [{required: true, message: ""}]}}
        }
    ];
    copy_plan_modal_field = [
        {
            label: "",
            field: "old_name",
            props: {disabled: true, decorator: {}}
        },
        {
            label: "",
            field: "old_version_id",
            props: {disabled: true, decorator: {}}
        },
        {
            label: "",
            field: "name",
            props: {decorator: {rules: [{required: true, message: ""}]}}
        },
        {
            label: "",
            field: "version_id",
            props: {decorator: {rules: [{required: true, message: ""}]}}
        }
    ];


    onExpand = expandedKeys => {
        this.setState({
            expandedKeys,
            autoExpandParent: false,
        });
    };

    onCheck = checkedKeys => {
        // console.log('onCheck', checkedKeys);
        this.updateCheckedKeys(checkedKeys)
    };

    onSelect = (selectedKeys, info) => {
        // console.log('onSelect', selectedKeys);
        this.setState({selectedKeys});
    };

    onAllSelected = e => {
        let checkedKeys = [];
        if (e.target.checked) {
            const item = this.getVersionByMenuItem();
            const pages = item && item.pages;
            if (pages) {
                checkedKeys = Object.keys(this.flat_pages_data);
            }
        }
        this.onCheck(checkedKeys)

    };


    renderTreeNodes = data =>
        data && data.map(item => {
            if (item.children) {
                return (
                    <TreeNode title={item.title} key={item.key} dataRef={item}>
                        {this.renderTreeNodes(item.children)}
                    </TreeNode>
                );
            }
            return <TreeNode key={item.key} {...item} />;
        });

    switchMenItem(menuItemId) {
        if (!menuItemId) menuItemId = this.state.menuItemId;
        const menuItem = this.getMenuItem(menuItemId);
        const nodes = menuItem && menuItem.selected_page_node;
        this.setState({menuItemId, checkedKeys: nodes, selectedKeys: [], menuSelectedKeys: [menuItemId]});
        this.onCheckAllStatus(menuItemId);
    }

    onCheckAllStatus(menuItemId = this.state.menuItemId) {
        const menuItem = this.getMenuItem(menuItemId);
        let allCheckStatus = false;
        if (menuItem) {
            this.flat_pages_data = {};
            const nodes = menuItem && menuItem.selected_page_node;
            const version = menuItem && this.getVersionByMenuItem(menuItem);
            const pages = menuItem ? version && version.pages : void 0;
            if (pages) {
                flatListByKey(pages, this.flat_pages_data);
                if (nodes && nodes.length === Object.keys(this.flat_pages_data).length) {
                    allCheckStatus = true;
                }
            }
        }
        this.setState({allCheckStatus})
    }


    showAddPlan() {
        if (this.state.versions.length === 0) {
            notification.open({
                message: "",
                description: ''
            });
            return
        }
        this.plan_modal_field[1].selectData = this.state.versions.filter(item=>!item.is_del).map(item => ({
            name: item.name,
            value: item.version_id
        }));
        this.setState({showAddPlanModal: true})
    }

    showAddSubPlan() {
        const item = this.getMenuItem();
        const version = this.getVersionByMenuItem();
        this.sub_plan_modal_field[0].props.decorator.initialValue = item && item.name;
        this.sub_plan_modal_field[1].props.decorator.initialValue = version && version.name;
        this.setState({showAddSubPlanModal: true})
    }

    showCopyPlan() {

        const item = this.getMenuItem();
        const version = this.getVersionByMenuItem();

        this.copy_plan_modal_field[0].props.decorator.initialValue = item && item.name;
        this.copy_plan_modal_field[1].props.decorator.initialValue = version && version.name;
        this.copy_plan_modal_field[2].props.decorator.initialValue = "Copy Of " + item && item.name;
        this.copy_plan_modal_field[3].disabled = item.parent_id;
        this.copy_plan_modal_field[3].selectData = this.state.versions.filter(item=>!item.is_del).map(item => ({
            name: item.name,
            value: item.version_id
        }));
        this.setState({showCopyPlanModal: true})
    }

    //=====================request======================//
    componentDidMount() {
        this.setState({loading: true});
        this.get_pages();
        this.get_plans();
    }

    get_plans() {
        request.get('/get_plans', null)
            .then((res) => {
                const data = res.data;
                let menuItemId = void 0;

                if (data) {
                    this.flat_menu_data = {};
                    flatListById(data, this.flat_menu_data);
                    menuItemId = data[0] && data[0].plan_id;
                }
                if (this.getMenuItem()) {
                    menuItemId = this.state.menuItemId
                }
                this.setState({data});
                this.switchMenItem(menuItemId)
            }).finally(() => this.setState({loading: false}));
    }

    get_pages() {
        request
            .get('/get_plan_version_pages', null)
            .then((res) => {
                let data = res.data ? res.data : [];
                this.setState({versions: data});
                this.onCheckAllStatus()
            })
    }

    onAddPlan(values, process, isRoot = true) {
        const item = this.getMenuItem();
        const parent_info = {};
        if (!isRoot) {
            if (!item) {
                notification.open({
                    message: "",
                    description: ''
                });
                return
            }
            parent_info.parent_id = item.plan_id;
            parent_info.version_id = item.version_id
        }
        request
            .post('/add_plan', {...values, ...parent_info})
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    process(true);
                    this.setState({showAddPlanModal: false, showAddSubPlanModal: false});
                    this.get_plans()
                }
            })
            .finally(() => process());
    }

    onCopyPlan(values, process) {
        const item = this.getMenuItem();
        const old_info = {};
        if (item) old_info.plan_id = item.plan_id;
        request
            .post('/copy_plan', {...values, ...old_info})
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    process(true);
                    this.setState({showCopyPlanModal: false});
                    this.get_plans()
                }
            })
            .finally(() => process());
    }

    onDelPlan = () => {
        request
            .post('/del_plan', {plan_id: this.state.menuItemId,version_id:this.getMenuItem().version_id})
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    this.get_plans()
                }
            })
    };


    onSavePageData(source, process) {
        request
            .post('/save_plan_data', {
                plan_id:this.getMenuItem().plan_id,
                data: source
            })
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    process();
                    this.get_plans()
                }
            })
    }

    onDelPageData(delItems, process) {
        request
            .post('/del_plan_data', {
                plan_id: this.getMenuItem().plan_id,
                data: delItems
            })
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    process();
                    this.get_plans()
                }
            })
    }

    updateCheckedKeys(checkedKeys) {

        request
            .post('/update_selected_page_node', {plan_id: this.getMenuItem().plan_id, selected_page_node: checkedKeys})
            .then((res) => {
            }).finally(() => this.get_plans());
    }

    getMenuItem(menuItemId = this.state.menuItemId) {
        return this.flat_menu_data[menuItemId]
    }

    getVersionByMenuItem(menuItem = this.getMenuItem()) {
        if (menuItem)
            return this.state.versions && this.state.versions.find(item => item.version_id === menuItem.version_id)
    }

    render() {
        const {expandedKeys, autoExpandParent, checkedKeys, selectedKeys} = this.state;

        const version = this.getVersionByMenuItem();
        const page = version && version.pages;
        const optionMenus = [{key: 'add', name: ""}];
        const menuItem = this.getMenuItem();
        // console.log('menuItem',menuItem);

        let node = undefined;
        if (menuItem) {
            optionMenus.push({key: 'add_sub', name: ""});
            optionMenus.push({key: 'copy', name: ""});
            optionMenus.push({key: 'del', name: ""});
            node = {title: menuItem.name, ...menuItem, key: menuItem.plan_id, disable: false, emptyText: ""};
        }

        function getKeyInArray(array) {
            const key = array[0];
            if (Array.isArray(key))
                return getKeyInArray(key);
            else return key
        }

        if (selectedKeys && selectedKeys.length > 0) {
            const key = getKeyInArray(this.state.selectedKeys);
            console.log(key);
            const node_info = this.flat_pages_data[key];
            console.log(this.flat_pages_data);
            console.log(node_info);
            if (node_info) node = {...node_info, disable: true, emptyText: ""}
        }

        const loop = data =>
            data && data.map(item => {
                if (item.children) {
                    return (
                        <TreeNode key={item.plan_id} title={item.name}>
                            {loop(item.children)}
                        </TreeNode>
                    );
                }
                return <TreeNode key={item.plan_id} title={item.name}/>;
            });

        return (
            <Layout style={{padding: '2vh', height: "90vh", display: "flex", flexDirection: "column"}}>
                <Content style={{background: "#fff", flex: 1}}>
                    <Row type={'flex'}>
                        <Col span={5}
                             style={{
                                 height: "88vh",
                                 display: "flex",
                                 flexDirection: "column",
                                 borderRight: "0.1px solid",
                                 borderColor: "#efefef",
                             }}>
                            <div style={{width: "100%", height: '6vh'}}>

                                <div
                                    style={{
                                        width: "100%",
                                        height: "100%",
                                        display: "flex",
                                        paddingTop: '2vh',
                                        flexDirection: "column",
                                        alignItems: 'center',
                                        justifyContent: "center",
                                        zIndex: 0
                                    }}>
                                    <p style={{fontSize: 15, color: "#777"}}></p>
                                </div>
                                <RightClickMenu
                                    menu={optionMenus}
                                    onClick={(e) => {
                                        if (e.key === "add") this.showAddPlan();
                                        if (e.key === "add_sub") this.showAddSubPlan();
                                        else if (e.key === 'copy') this.showCopyPlan();
                                        else if (e.key === 'del') {
                                            const item = this.flat_menu_data[this.state.menuItemId]
                                            confirm({
                                                title: `${item && item.name}?`,
                                                content: '',
                                                onOk: this.onDelPlan,
                                            });
                                        }
                                    }}>
                                    <Icon type={'menu'}
                                          style={{position: 'absolute', top: '2.3vh', right: 16, zIndex: 2}}/>
                                </RightClickMenu>

                            </div>
                            <div className={'hide-scrollbar'} style={{height: '80vh', overflowY: 'scroll'}}>

                                {
                                    this.state.data && this.state.data.length > 0 ?
                                        <Tree theme={'light'}
                                              selectedKeys={this.state.menuSelectedKeys}
                                              onSelect={(selectedKeys) => this.switchMenItem(selectedKeys[0], this.state.data)}
                                        >
                                            {loop(this.state.data)}
                                        </Tree> : <div><Empty image={Empty.PRESENTED_IMAGE_SIMPLE}
                                                              style={{marginTop: "20vh"}}
                                                              description={""}/>
                                        </div>
                                }
                            </div>
                        </Col>
                        <Col span={19} style={{
                            height: "84vh",
                            display: "flex",
                            flexDirection: "row"
                        }}>
                            <div style={{padding: 24, display: "flex", flexDirection: "column", flex: 1}}>
                                <div style={{
                                    display: 'flex', flexDirection: 'row', alignItems: 'center',
                                    justifyContent: "space-between"
                                }}>
                                    <p style={{fontSize: 15, color: "#777", margin: 16}}></p>
                                    {((menuItem && menuItem.children.length === 0)) &&
                                    <Checkbox onChange={this.onAllSelected}
                                              checked={this.state.allCheckStatus}
                                              style={{padding: 2}}></Checkbox>}
                                </div>
                                <div className={'hide-scrollbar'} style={{overflowY: 'scroll', flex: 1}}>
                                    {
                                        (menuItem && menuItem.children.length === 0) && page ? <Tree
                                            onExpand={this.onExpand}
                                            expandedKeys={expandedKeys}
                                            autoExpandParent={autoExpandParent}
                                            onCheck={this.onCheck}
                                            checkedKeys={checkedKeys}
                                            onSelect={this.onSelect}
                                            selectedKeys={this.state.selectedKeys}
                                            checkable>
                                            {(menuItem && menuItem.children.length === 0) && this.renderTreeNodes(page)}
                                        </Tree> : <div><Empty image={Empty.PRESENTED_IMAGE_SIMPLE}
                                                              style={{marginLeft: "-64%"}}
                                                              description={menuItem && menuItem.children.length > 0 ? "" : ""}/>
                                        </div>
                                    }
                                </div>
                            </div>
                            <div style={{width: 500, overflowY: 'scroll', flex: 1}}>
                                {
                                    menuItem && <ConfigPageField
                                        disable={node.disable}
                                        emptyText={node.emptyText}
                                        onDel={(delItems, process) => this.onDelPageData(delItems, process)}
                                        onSave={(source, process) => this.onSavePageData(source, process)}
                                        pageInfo={node}
                                    />
                                }
                            </div>
                        </Col>
                    </Row>
                </Content>

                <FromModal
                    title=""
                    visible={this.state.showAddPlanModal}
                    onOk={(values, process) => this.onAddPlan(values, process)}
                    fields={this.plan_modal_field}
                    onCancel={() => this.setState({showAddPlanModal: false})}
                />
                <FromModal
                    title=""
                    visible={this.state.showAddSubPlanModal}
                    onOk={(values, process) => this.onAddPlan(values, process, false)}
                    fields={this.sub_plan_modal_field}
                    onCancel={() => this.setState({showAddSubPlanModal: false})}
                />
                <FromModal
                    title=""
                    visible={this.state.showCopyPlanModal}
                    onOk={(values, process) => this.onCopyPlan(values, process)}
                    fields={this.copy_plan_modal_field}
                    onCancel={() => this.setState({showCopyPlanModal: false})}
                />
            </Layout>
        );
    }


}
