import * as React from "react";
import {Anchor, Checkbox, Layout, Table,} from "antd";
import Typography from "antd/es/typography";
import {flat_data, guideData} from "./guideData";

const {Title, Paragraph} = Typography;
const {Link} = Anchor;
const {Content} = Layout;

const flatData = [];
flat_data(guideData, flatData);

export class Guide extends React.Component {

    loop = (data) => {
        if (!data) return;
        return data.map((item => {
            return <Link href={`#${item.id || item.title}`} title={item.title} children={this.loop(item.children)}/>
        }))
    };

    render() {

        return (

            <Layout id='content' style={{padding: '2vh', display: "flex", flexDirection: "column"}}>
                <Content style={{background: "#fff", flex: 1}}>
                    <div
                        style={{display: "flex", flexDirection: "row"}}
                    >
                        <div
                            style={{
                                display: "flex",
                                padding: 16,
                                flex: 5,
                                flexDirection: "column",
                                borderRight: "0.1px solid",
                                borderColor: "#efefef",
                            }}>

                            <Anchor getContainer={() => document.getElementById('rootLayout')}
                            >
                                {this.loop(guideData)}
                            </Anchor>
                        </div>
                        <div style={{
                            display: "flex",
                            padding: 16,
                            flex: 19,
                            flexDirection: "column"
                        }}>
                            {flatData.map(item => <Doc data={item}/>)}
                        </div>
                    </div>
                </Content>
            </Layout>
        );
    }

}

const paramsColumns = [
    {
        title: '',
        dataIndex: 'name',
        key: 'name'
    },
    {
        title: '',
        dataIndex: 'desc',
        key: 'desc'
    },
    {
        title: '',
        dataIndex: 'value',
        key: 'value'
    },
];

function Doc({data}) {

    let params = undefined;
    if (data.params && data.params.length > 0) {
        params = [];
        data.params.forEach(item => {
            params.push({name: item[0], desc: item[1] ?? '', value: item[2] ?? ''})
        })
    }
    let support_ios = true, support_android = true;
    if (data.support_platform) {
        support_ios = data.support_platform.ios;
        support_android = data.support_platform.android
    }

    let support_airtest = true, support_poco = true;
    if (data.support_framework) {
        support_airtest = data.support_framework.airtest;
        support_poco = data.support_framework.poco;
    }

    return (<a name={data.id || data.title} style={{margin: 16}}>
        <Typography>
            <Title level={4}>{data.title}</Title>
            {data.desc && <Paragraph>{data.desc}</Paragraph>}

            {
                data.params && <React.Fragment>
                    <span>:</span>
                    <br/>
                    <Checkbox defaultChecked={support_ios} disabled>IOS</Checkbox>
                    <br/>
                    <Checkbox defaultChecked={support_android} disabled>Android</Checkbox>
                    <br/>
                    <br/>
                </React.Fragment>
            }
            {
                data.params && <React.Fragment>
                    <span>:</span>
                    <br/>
                    <Checkbox defaultChecked={support_poco} disabled>POCO</Checkbox>
                    <br/>
                    <Checkbox defaultChecked={support_airtest} disabled>Airtest</Checkbox>
                </React.Fragment>
            }
        </Typography>

        {data.imgs && data.imgs.map(item => (
            <div style={{marginTop: 16}}>
                <p>{item[1]}</p>
                <img src={item[0]} alt="" style={{width: '80%'}}/>
            </div>
        ))}
        {params &&
        <Table style={{marginTop: 16}} columns={paramsColumns} dataSource={params} size={'small'} title={() => ''}
               pagination={false}/>}
    </a>);
}
