import {notification} from 'antd';
import Strings from "./strings";

// import {getCurrentLocal} from '@/i18n';

/**
 *  errorTio > resData.message > error.message > ''
 *
 * @param error
 * @param errorTip
 * @returns {*}
 */
function getErrorTip({error, errorTip}) {

    if (errorTip && errorTip !== true) return errorTip;

    if (error && error.response) {
        const {status, message} = error.response;


        // 
        if (message) return message;

        if (status === 403) {
            return Strings.noAccess;
        }

        if (status === 404) {
            return Strings.notFound;
        }

        if (status === 504) {
            return Strings.serverBusy;
        }

        if (status === 500) {
            return Strings.serverBusy;
        }
    }

    if (error && error.message && error.message.startsWith('timeout of')) return Strings.timeOut;

    if (error) return error.message;

    return Strings.serverBusy;
}

export default function handleError({error, errorTip}) {

    if (errorTip === false) return;

    const description = getErrorTip({error, errorTip});

    notification.error({
        message: Strings.error,
        description,
    });
}
