import {Component} from "react";
import {BrowserRouter as Router, Redirect, Route, Switch} from "react-router-dom";
import React from "react";
import {Application} from "./application";
// import {createBrowserHistory} from "history";
import TestPages from "./pages/test-pages";
import TestPlans from "./pages/test-plans";
import TestExec from "./pages/test-exec";
import {Guide} from "./pages/guide";

// const history = createBrowserHistory();

export class App extends Component {

    render() {
        return (
            <div style={{display: 'flex', flexDirection: 'column', position: 'relative', height: '100vh'}}>
                <Router>
                    <Switch>
                        <Route path={'/'} render={props => (<Application {...props}>
                            <Switch>
                                <Route exact path={'/test_pages'} component={TestPages}/>
                                <Route exact path={'/test_plans'} component={TestPlans}/>
                                <Route exact path={'/test_exec'} component={TestExec}/>
                                <Route exact path={'/guide'} component={Guide}/>
                                <Redirect path={'/'} to={'/test_pages'}/>
                            </Switch>
                        </Application>)}/>
                    </Switch>
                </Router>
            </div>
        );
    }
}
