import * as React from "react";
import * as antd from "antd";
import {Col, Icon, Layout, Menu, Row, Modal} from "antd";
import {generateList, getParentKey} from "../../library/tree-utils";
import FromModal from '../../components/FromModal'
import RightClickMenu from '../../components/RightClickMenu'
import ConfigPageField from "../../components/config-page/ConfigPageField";
import BasePages from "../base/BasePages";
import {request} from "../../library/request";
import {Empty} from "antd";

const {Content} = Layout;
const {Tree, Input} = antd;
const {TreeNode} = Tree;
const {Search} = Input;
const {confirm} = Modal;


export default class TestPages extends BasePages {

    dataList = [];
    state = {
        expandedKeys: [],
        searchValue: '',
        autoExpandParent: true,
        collapsed: false,
        data: undefined,
        menuItemIndex: 0,
        pages: undefined,
        nodeKey: undefined,
        showAddVersionModal: false,
        showCopyVersionModal: false,
        confirmLoading: false,
    };
    version_modal_field = [
        {
            label: "",
            field: "version_name",
            props: {decorator: {rules: [{required: true, message: ""}]}}
        },
        {
            label: "",
            field: "path",
            props: {decorator: {rules: [{required: true, message: ""}]}}
        }
    ];
    copy_version_modal_field = [
        {
            label: "",
            field: "version_name",
            props: {decorator: {rules: [{required: true, message: ""}]}}
        },
        {
            label: "",
            field: "old_path",
            props: {disabled: true, decorator: {}}
        },
        {
            label: "",
            field: "path",
            props: {decorator: {}}
        }
    ];

    onExpand = expandedKeys => {
        this.setState({
            expandedKeys,
            autoExpandParent: false,
        });
    };

    onChange = e => {
        const {value} = e.target;
        const expandedKeys = this.dataList
            .map(item => {
                if (item.title.indexOf(value) > -1) {
                    return getParentKey(item.key, this.state.pages);
                }
                return null;
            })
            .filter((item, i, self) => item && self.indexOf(item) === i);
        this.setState({
            expandedKeys,
            searchValue: value,
            autoExpandParent: true,
        });
    };

    onSelected = e => {
        const nodeKey = this.getNodeByKey(e[0]) && this.getNodeByKey(e[0]).key;
        this.setState({nodeKey})
    };

    getNodeByKey(key) {
        const item = this.dataList.find(item => item.key === key);
        return item && item.data;
    }

    showCopyVersionModal() {
        const menu = this.state.data[this.state.menuItemIndex];
        this.copy_version_modal_field[0].props.decorator.initialValue = "Copy of " + menu.name;
        this.copy_version_modal_field[1].props.decorator.initialValue = menu.path;
        this.copy_version_modal_field[2].props.decorator.initialValue = menu.path;
        this.setState({showCopyVersionModal: true})
    }

    //=====================request========================================>
    onSavePageData(source, process) {
        request.post('/save_page_data', {
            version_id: this.state.data[this.state.menuItemIndex].version_id,
            node_key: this.state.nodeKey,
            data: source
        })
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    process();
                    this.get_pages()
                }
            })
            .finally(() => this.setState({loading: false}));
    }

    onDelPageData(delItems, process) {
        request.post('/del_page_data', {
            version_id: this.state.data[this.state.menuItemIndex].version_id,
            node_key: this.state.nodeKey,
            data: delItems
        })
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    process();
                    this.get_pages()
                }
            })
            .finally(() => this.setState({loading: false}));
    }

    onAddVersion(values, process) {
        request.post('/add_version', values)
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    process(true);
                    this.setState({showAddVersionModal: false});
                    this.get_pages()
                }
            })
            .finally(() => process());
    }

    onCopyVersion(values, process) {
        values.version_id = this.state.data[this.state.menuItemIndex].version_id;
        if (!values.path) values.path = values.old_path;
        request
            .post('/copy_version', values)
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    process(true);
                    this.setState({showCopyVersionModal: false});
                    this.get_pages()
                }
            })
            .finally(() => process());
    }

    onDelVersion = () => {
        this.setState({loading: true});
        request.post('/del_version', {version_id: this.state.data[this.state.menuItemIndex].version_id})
            .then((res) => {
                const code = res.code;
                if (code === 1) {
                    this.get_pages()
                }
            })
            .finally(() => this.setState({loading: false}));
    };


    componentDidMount() {
        this.setState({loading: true});
        this.get_pages()
    }

    get_pages() {
        request
            .get('/get_version_pages', null)
            .then((res) => {
                const data = res.data;
                const menuItemIndex = Math.min(this.state.menuItemIndex, data.length - 1);
                this.switchMenItem(menuItemIndex, data);
            })
            .finally(() => this.setState({loading: false}));
    }

    switchMenItem(menuItemIndex, data) {
        if (menuItemIndex < 0) menuItemIndex = 0;
        const item = data[menuItemIndex];
        const pages = item && item.pages;
        this.dataList = [];
        if (pages)
            generateList(pages, this.dataList);
        this.setState({pages, menuItemIndex, data})
    }

    //=====================request========================================>
    render() {
        const {searchValue, expandedKeys, autoExpandParent, data, pages} = this.state;
        console.log('pages', pages);
        console.log('data', data);
        console.log('index', this.state.menuItemIndex);
        const optionMenus = [{key: 'add', name: ""}];
        if (data && data.length > 0) {
            optionMenus.push({key: 'copy', name: ""});
            optionMenus.push({key: 'del', name: ""})
        }
        const node = this.getNodeByKey(this.state.nodeKey);
        const loop = data =>
            data && data.map(item => {
                const index = item.title.indexOf(searchValue);
                const beforeStr = item.title.substr(0, index);
                const afterStr = item.title.substr(index + searchValue.length);
                const title = index > -1 ? (
                        <span>{beforeStr}<span style={{color: '#f50'}}>{searchValue}</span>{afterStr}</span>) :
                    (<span>{item.title}</span>);
                if (item.children) {
                    return (
                        <TreeNode key={item.key} title={title}>
                            {loop(item.children)}
                        </TreeNode>
                    );
                }
                return <TreeNode key={item.key} title={title}/>;
            });
        return (

            <Layout style={{padding: '2vh', height: "90vh", display: "flex", flexDirection: "column"}}>
                <Content style={{background: "#fff", flex: 1}}>
                    <Row type={'flex'}>
                        <Col span={5}
                             style={{
                                 height: "88vh",
                                 display: "flex",
                                 flexDirection: "column",
                                 borderRight: "0.1px solid",
                                 borderColor: "#efefef",
                             }}>

                            <div style={{width: "100%", height: '6vh'}}>

                                <div
                                    style={{
                                        width: "100%",
                                        height: "100%",
                                        display: "flex",
                                        paddingTop: '2vh',
                                        flexDirection: "column",
                                        alignItems: 'center',
                                        justifyContent: "center",
                                        zIndex: 0
                                    }}>
                                    <p style={{fontSize: 15, color: "#777"}}></p>
                                </div>
                                <RightClickMenu
                                    menu={optionMenus}
                                    onClick={(e) => {
                                        if (e.key === "add") this.setState({showAddVersionModal: true});
                                        else if (e.key === 'copy') this.showCopyVersionModal();
                                        else if (e.key === 'del') {
                                            confirm({
                                                title: `${data[this.state.menuItemIndex].name}?`,
                                                content: '',
                                                onOk: this.onDelVersion,
                                            });
                                        }
                                    }}>
                                    <Icon type={'menu'}
                                          style={{position: 'absolute', top: '2.3vh', right: 16, zIndex: 2}}/>
                                </RightClickMenu>

                            </div>

                            <div className={'hide-scrollbar'} style={{height: '80vh', overflowY: 'scroll'}}>
                                {
                                    data && data.length > 0 ? <Menu theme={'light'} defaultSelectedKeys={['0']}>

                                        {
                                            data.map((item, index) => (

                                                <Menu.Item key={`${index}`}
                                                           onClick={() => this.switchMenItem(index, data)}>
                                                    <Icon type="file"/>
                                                    <span>{item.name}</span>
                                                </Menu.Item>

                                            ))
                                        }
                                    </Menu> : <div><Empty image={Empty.PRESENTED_IMAGE_SIMPLE}
                                                          style={{marginTop:"20vh"}}
                                                          description={""}/>
                                    </div>
                                }
                            </div>
                        </Col>
                        <Col span={19} style={{
                            height: "84vh",
                            display: "flex",
                            flexDirection: "row"
                        }}>
                            <div style={{padding: 24, display: "flex", flexDirection: "column", flex: 1}}>
                                {
                                    (data && data.length !== 0) && pages && <Search style={{marginLeft: 16, width: "80%", height: 32}} placeholder="Search"
                                            onChange={this.onChange}/>
                                }
                                <div className={'hide-scrollbar'} style={{overflowY: 'scroll', flex: 1}}>
                                    {
                                        (data && data.length !== 0) && pages &&
                                        <Tree
                                            onExpand={this.onExpand}
                                            expandedKeys={expandedKeys}
                                            autoExpandParent={autoExpandParent}
                                            onSelect={(item) => this.onSelected(item)}
                                        >
                                            {loop(pages)}
                                        </Tree>}
                                </div>
                            </div>
                            <div style={{width: 500, overflowY: 'scroll', flex: 1}}>
                                {
                                    ((node && node.type === "class") || (node && node.type === "method")) ?
                                        <ConfigPageField
                                            pageInfo={node}
                                            onDel={(delItems, process) => this.onDelPageData(delItems, process)}
                                            onSave={(source, process) => this.onSavePageData(source, process)}
                                        /> : void 0
                                }
                            </div>
                        </Col>
                    </Row>
                </Content>
                <FromModal
                    title=""
                    visible={this.state.showAddVersionModal}
                    onOk={(values, process) => this.onAddVersion(values, process)}
                    fields={this.version_modal_field}
                    onCancel={() => this.setState({showAddVersionModal: false})}
                />
                <FromModal
                    title=""
                    visible={this.state.showCopyVersionModal}
                    onOk={(values, process) => this.onCopyVersion(values, process)}
                    fields={this.copy_version_modal_field}
                    onCancel={() => this.setState({showCopyVersionModal: false})}
                />
            </Layout>);

    }


}
